    <div class="statistics_wra">
      <div class="itemModule">
        <div class="title flex">
          <img src="../../assets/img/deviceManage/title_03.png" />
          <span>维修任务状态统计</span>
        </div>
        <div class="topBox">
          <div class="itemChunk">
            <span class="value">{{ repairCard.no }}</span>
            <span class="name">未开始</span>
          </div>
        </div>
        <div class="bottonBox">
          <div class="itemChunk">
            <span class="value">{{ repairCard.do }}</span>
            <span class="name">维修中</span>
          </div>
          <div class="itemChunk">
