          <div class="itemChunk">
            <span class="value">{{ checkCard.no }}</span>
            <span class="name">未开始</span>
          </div>
        </div>
        <div class="flex-box bottonBox">
          <div class="itemChunk">
            <span class="value">{{ checkCard.do }}</span>
            <span class="name">点检中</span>
          </div>
          <div class="itemChunk">
            <span class="value">{{ checkCard.off }}</span>
            <span class="name">已完成</span>
          </div>
        </div>
      </div>
    </div>
    <div class="list_wra">
      <div class="repair">
        <div class="kanban_module_title">维修任务列表</div>
        <div class="table_wra">
          <table id="repairTable">
            <tr>
              <th>设备名称</th>
              <th>故障类型</th>
              <th>报修人员</th>
              <th>报修时间</th>
              <th>维修状态</th>
            </tr>
            <tr v-for="(item, index) in repairList" :key="index">
              <td>{{ item.deviceName }}</td>
              <td>{{ item.repairTypeNames }}</td>
              <td>{{ item.maintainUserName }}</td>
              <td>{{ item.occurTime }}</td>
              <td>
                <span
                  class="state"
                  :class="[item.repairState === 1 ? 'no' : 'doing']"
                ></span
                >{{ repairStateFunc(item.repairState) }}
              </td>
            </tr>
          </table>
        </div>
      </div>
      <div class="maintain">
        <div class="kanban_module_title">保养任务列表</div>
        <div class="table_wra">
