              <th>保养周期</th>
              <th>预计保养时间</th>
              <th>保养状态</th>
            </tr>
            <tr v-for="(item, index) in maintainList" :key="index">
              <td>{{ item.deviceName }}</td>
              <td>{{ item.cycleName }}</td>
              <td>{{ timeFunc(item.maintainTime) }}</td>
              <td>
                <span
                  class="state"
                  :class="[item.repairState === 1 ? 'doing' : 'no']"
                ></span>
                {{ maintainStateFunc(item.maintainState) }}
              </td>
            </tr>
          </table>
        </div>
      </div>
