<script>
import { fontSize } from '@/utils/common';
import { STATUS_CODE } from '@/utils/constantsFile';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';
import {
  deviceManagerGetCheckCount,
  deviceManagerGetRepairTaskCount,
  deviceManagerGetMaintainTaskCount,
  getRepairTask,
  getMaintainTask
} from '@/api/kanban';
export default {
  name: 'deviceManage',
  data() {
    return {
      kanbanConfig: {},
      repairCard: {
        no: 0,
        do: 0,
        off: 0
      },
      maintainCard: {
        no: 0,
        do: 0,
        off: 0
      },
      checkCard: {
        no: 0,
        do: 0,
        off: 0
      },
      current_r: 1,
      size_r: 5,
      current_m: 1,
      size_m: 5,
      repairList: [],
      maintainList: [],
      timer: null
    };
  },
  created() {
    document.documentElement.style.fontSize = fontSize() + 'px';
    window.onresize = () => {
      document.documentElement.style.fontSize = fontSize() + 'px';
    };
    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      this.kanbanConfig =
        KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }
    this.getStatisticsData();
    this.showRepairList();
    this.showMaintainList();
    this.timer = setInterval(() => {
      this.getStatisticsData();
      this.showRepairList();
      this.showMaintainList();
    }, this.kanbanConfig?.time || 15000);
  },
  destroyed() {
    this.timer && clearInterval(this.timer);
  },
  methods: {
    timeFunc(time) {
      return time.substring(0, 11);
    },
    async getStatisticsData() {
      try {
        const res = await deviceManagerGetCheckCount(
          {},
          {
            secretKey: this.kanbanConfig.key
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (res.rows.waitCount === undefined) {
            this.checkCard.no = 0;
            this.checkCard.do = 0;
            this.checkCard.off = 0;
          } else {
            this.checkCard.no = res.rows.waitCount;
            this.checkCard.do = res.rows.ingCount;
            this.checkCard.off = res.rows.finishCount;
          }
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      }

      try {
        const res = await deviceManagerGetRepairTaskCount(
          {},
          {
            secretKey: this.kanbanConfig.key
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.repairCard.no = res.rows.waitCount;
          this.repairCard.do = res.rows.ingCount;
          this.repairCard.off = res.rows.finishCount;
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      }

      try {
        const res = await deviceManagerGetMaintainTaskCount(
          {},
          {
            secretKey: this.kanbanConfig.key
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.maintainCard = {
            no: res.rows.waitCount,
            do: res.rows.ingCount,
            off: res.rows.finishCount
          };
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      }
    },
    // 获取维修任务列表
    async showRepairList() {
      var condtion = {
        conditionJson1: [
          {
            column: 'repairState',
            operator: 1,
            ptype: 'int',
            type: 'in',
            value: '1,2'
          }
        ],
        current: this.current_r,
        size: this.size_r,
        secretKey: this.kanbanConfig.key
      };
      try {
        const res = await getRepairTask({}, condtion);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (this.current_r * this.size_r >= res.total) {
            this.current_r = 1;
          } else {
            this.current_r++;
          }
          this.repairList = res.rows;
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    // 获取保养任务列表
    async showMaintainList() {
      var condtion = {
        conditionJson1: [
          {
            column: 'maintainState',
            operator: 1,
            ptype: 'int',
            type: 'in',
            value: '1,4'
          }
        ],
        current: this.current_m,
        size: this.size_m,
        secretKey: this.kanbanConfig.key
      };
      try {
        const res = await getMaintainTask({}, condtion);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (this.current_m * this.size_m >= res.total) {
            this.current_m = 1;
          } else {
            this.current_m++;
          }
          this.maintainList = res.rows;
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return (
        decodeURIComponent(
          (new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(
            url
          ) || ['', ''])[1].replace(/\+/g, '%20')
        ) || null
      );
    },
    repairStateFunc(state) {
      if (state === 1) {
        return '待维修';
      } else if (state === 2) {
        return '维修中';
      }
    },
    maintainStateFunc(state) {
      if (state === 1) {
        return '保养中';
      } else if (state === 4) {
        return '待保养';
      }
    }
  }
};
</script>

